import React from 'react';
import { CompleteIcon, DeleteIcon } from '../TodoIcon/Icons';
import './TodoItem.css';

function TodoItem({ todo, onComplete, onDelete }) {

    return (
        <li className="TodoItem">
            <CompleteIcon
                completed={todo.completed}
                onComplete={onComplete}
            />
            <p className={`TodoItem-p ${todo.completed && 'TodoItem-p--complete'}`}>
                {todo.text}
            </p>
            <DeleteIcon
                onDelete={onDelete}
            />
        </li>
    );
}

export { TodoItem };